.app-entry {
	height: 100%;

	max-width: 900px;
	min-height: 400px;

	margin: 0 auto;

	-webkit-box-sizing: border-box;

	        box-sizing: border-box;

	position: relative;
}

.app-entry__content {
	position: absolute;
	left: 0;
	top: 10px;
	right: 0;
	bottom: 10px;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;

	border: 2px #ececec solid;
	padding: 2px;
	-webkit-box-shadow: 0 0 20px rgba(0,0,0,.3);
	        box-shadow: 0 0 20px rgba(0,0,0,.3);

	overflow: hidden;
}

.app-entry__conversations {
	width: 300px;
	height: 100%;

	-ms-flex: 0 0 auto;

	    flex: 0 0 auto;
	overflow: hidden;
}

.app-entry__conversations-header {
	height: 40px;
	background-color: #fff;
}

.app-entry__conversations__m-hidden {
	width: 0 !important;
}

.app-entry__current-conversation {
	-ms-flex: 1 1 auto;
	    flex: 1 1 auto;

	height: 100%;

	background: #d3e7ec;
}

.app__m-small-screen .app-entry__content {
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;

	border: none;
	padding: 0;
	-webkit-box-shadow: none;
	        box-shadow: none;
}

.app__m-small-screen .app-entry__conversations {
	width: 100%;
}



